<template>
 <div style="padding: 30px;">
    <!-- <Breadcrumb :routes="routes"/> -->
    <div class="module-container">
      <el-card v-for="item in modules" :key="item.id">
        <el-image :src="item.image" class="module-image" @click="handleModuleClick(item)"></el-image>
        <div class="module-title">{{ item.title }}</div>
      </el-card>
      <!-- <router-view></router-view> -->
    </div>
 </div>
    
    
</template>

<script>
import Breadcrumb from '../../components/breadcrub/BreadCrub.vue'
export default {
  data() {
    return {
      modules: [
        { id: 1, image: require('../../assets/images/upload.png'), title: '成果上传(用户)' },
        { id: 2, image: require('../../assets/images/logo.png'), title: '成果审核(审核)' },
        { id: 3, image: require('../../assets/images/dataimport.png'), title: '数据处理入库' },
        // 添加更多的模块数据...
      ]
    };
  },
  components: {
    // Breadcrumb
  },
  computed: {},
  methods: {
    handleModuleClick(item) {
      // 处理模块的点击事件
      console.log('点击了模块', item);
      switch(item.title){
        case '成果上传(用户)':
            this.$router.push('/upload')
            break
        case '成果审核(审核)':
            this.$router.push('/check')
            break
        case '数据处理入库':
            this.$router.push('/dataSelect')
            break
        default:
            return
      }
    }
  }
};
</script>

<style scoped>
.module-container {
  display: flex;
}

.el-card {
  width: 200px;
  margin-right: 10px;
  cursor: pointer;
}

.module-image {
  height: 150px;
  /* object-fit: cover; 可以确保元素在保持其原始宽高比的同时填充满容器。 */
  object-fit: cover;
  transition: transform 0.3s ease;
}
.module-image:hover{
  transform: scale(1.05,1.05);
}
.module-title {
  margin-top: 10px;
  text-align: center;
}
</style>

